<template>
  <v-container>
    <v-layout align-center justify-center>
      <v-responsive class="mx-auto col-12 col-sm-4">
        <v-card class="my-5 border rounded-3">
          <v-form @submit.prevent="resetPassword" ref="form" v-model="valid">
            <v-card-title class="bgred text-white rounded-top">Reset Password</v-card-title>
            <v-card-text>
              <v-text-field type="email" v-model="email" class="d-none"></v-text-field>
              <v-text-field type="password" v-model="password" :rules="passwordRules" :counter="15" placeholder="Enter New Password"></v-text-field>
              <v-text-field type="password" v-model="cpassword" :counter="15" :rules="[(password == cpassword) || 'Confirm password does not match']" placeholder="Confirm Password"></v-text-field>
              <div class="mt-1">
                <v-btn type="submit" color="primary" :disabled="!valid">Reset Password</v-btn>
              </div>
            </v-card-text>
          </v-form>
        </v-card>
      </v-responsive>
    </v-layout>
  </v-container>
</template>

<script>

import axios from "axios";

export default {
  name: "UserResetpassword",
  data() {
    return {
        email: '',
        password: '',
        cpassword: '',
        valid: false,
        passwordRules: [
          v => !!v || 'Password is required.',
          v => /[A-Z]/.test(v) || 'Use upper case.',
          v => /[a-z]/.test(v) || 'Use lower case.',
          v => /[0-9]/.test(v) || 'Use number.',
          v => /[#?!@$%^&*-]/.test(v) || 'Use special character.',
          v => v.length >= 8 || 'Password must be greater than 8 characters.',
          v => v.length <= 15 || 'Password must be less than 15 characters.',
        ],
    }
  },
  created() {
    const email1 = this.$route.params.email;
    this.email = email1;
  },
  methods: {
    resetPassword(){
      axios.post('resetpassword',{
            email: this.email,
            password: this.password,
      },)
          .then(response => {
            if(response.data.status == 200){
              this.$router.push({name: 'home'});
            }
          })
          .catch(error => {
            console.log(error.response.data);
          });
    }
  }
}
</script>

<style scoped>
.bgred {background-color: #fd0606;}
</style>